import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { to } from '../../../../platform/constants/routes';
import FooterItem from '../Footer-item';

import searchMedicine from '../../../../assets/images/01.png';
import findPharmacy from '../../../../assets/images/02.png';
import prespiction from '../../../../assets/images/03.png';
import question from '../../../../assets/images/04.png';

import './index.scss';

class Footer extends Component {
    render() {
        return (
            <footer className="G-flex">
                <Link to={to.searchMedicine}> <FooterItem
                    img={searchMedicine}
                    title="Search Medicine"
                    description="Lorem Ipsum is simply dummy text of the printing"
                /></Link>
                <Link to={to.searchMedicine}>
                    <FooterItem
                        img={findPharmacy}
                        title="Find pharmacy"
                        description="Lorem Ipsum is simply dummy text of the printing"
                    />
                </Link>
                <Link to={to.searchMedicine}>
                    <FooterItem
                        img={prespiction}
                        title="Prespiciton"
                        description="Lorem Ipsum is simply dummy text of the printing"
                    />
                </Link>
                <Link to={to.searchMedicine}>
                    <FooterItem
                        img={question}
                        title="Ask a question"
                        description="Lorem Ipsum is simply dummy text of the printing"
                    />
                </Link>

            </footer>
        )
    }
}

export default Footer;