import React, { Component } from 'react';
import SignIn from './components/SignIn';
import SignUp from './components/SingUp';

import './index.scss';


class AuthModal extends Component {

    
    state = {
        activeTab: false,
    }

    changeTabHandler = (bool) => {
        this.setState({
            activeTab: bool
        })
    }
    render(){
        const { activeTab } = this.state;

        return (<div className="F-sign-modal-container">
            <div className = "F-auth-titles">
                <h3 className = {!activeTab ? 'F-active-tab' : ''} onClick = {() => this.changeTabHandler(false)}>Sign in</h3>
                <h3 className = {activeTab ? 'F-active-tab' : ''} onClick = {() => this.changeTabHandler(true)}>Sign up</h3>

               
            </div>
            {!activeTab ? <SignIn /> : <SignUp />}
            </div>
        )
    }
}

export default AuthModal;