import React, { Component } from 'react';
import InputField from './InputField';

class SignUp extends Component {

    state = {
        next: false,
    }

    changePage = () => {
        console.log('works')
        this.setState = ({
            next: true,
        })
    }

    render() {
        const { next } = this.state;

        return (
            <div className="F-auth-container">
                <form className="F-auth-form">
                    <>
                        <InputField
                            name="First name"
                            type="text"
                        />
                        <InputField
                            name="Last name"
                            type="text"
                        />
                        <InputField
                            name="Email"
                            type="email"
                        />
                        <div className="F-auth-button">
                            <button onClick={this.changePage}>Next</button>
                        </div>
                    </>
                    <>
                        <InputField
                            name="Phone number"
                            type="number"
                        />
                        <InputField
                            name="City"
                            type="text"
                        />
                        <InputField
                            name="Address"
                            type="text"
                        />
                    </>
                    <>
                        <InputField
                            name="Password"
                            type="password"
                        />
                        <InputField
                            name="Confirm password"
                            type="password"
                        />
                        <div className="agree-field">
                        <label>
                            <input type="checkbox" />
                            <span>I agree to <a href="#">Fastmed terms of service </a></span>
                        </label>
                        </div>
                      
                    </>


                </form>
            </div>
        )
    }
}

export default SignUp;