import React, { Component } from 'react';
import InputField from './InputField';

class SignIn extends Component {
    render() {
        return (
            <div className="F-auth-container">
                <form className="F-auth-form">
                    <InputField
                        name="Log in"
                        type="text"
                    />
                    <InputField
                        name="Password"
                        type="password"
                    />

                    <div className="F-signin-footer">
                        <div className="remember-field ">
                            <label className="G-flex G-align-center">
                                <input type="checkbox" />
                                <span>Remember me</span>
                            </label>
                        </div>

                        <a href="#">Forget Password</a>
                    </div>

                    <div className="F-auth-button">
                        <button>Sign in</button>
                    </div>
                </form>
            </div>
        )
    }
}

export default SignIn;